import React, { Component } from "react";
import Page from "./page";
import styled from "styled-components";
import { profile, experience } from "../data/siteConfig";
import { GrMail } from "react-icons/gr";
import { FaMobileAlt } from "react-icons/fa";
import { age, Linkify } from "../utils/helpers";
import device from "../utils/device";
import moment from "moment";

const Wrapper = styled.div`
  padding: 1em;
`;
const BigHeading = styled.h1`
  margin: 0;
`;
const Heading = styled.h2`
  margin: 0;
`;
const SubHeading = styled.h3`
  margin: 0;
  text-decoration: underline;
  color: ${(props) => props.theme.backgroundColor};
`;
const SmallHeading = styled.h5`
  margin: 0;
  text-decoration: none;
  color: ${(props) => props.theme.backgroundColor};
`;
const Text = styled.p`
  margin: 0;
`;
const IconText = styled.div`
  align-items: center;
  display: flex;
`;
const HorizontalAligned = styled.section`
  @media ${device.tablet} {
    display: flex;
    justify-content: space-between;
  }
`;
const Button = styled.button`
  :hover {
    cursor: pointer;
    color: ${(props) => props.theme.backgroundColor};
  }
`;

const PersonalDetails = styled.div`
  transition: opacity 0.5s linear;
  display: ${(props) => (props.show ? "block" : "none")};
  opacity: ${(props) => (props.show ? 1 : 0)};
`;

const UnorderedList = (props) => {
  let exp = age(profile.dateOfStarting)[0];
  return (
    <ul>
      {props.content.map((item, index) => (
        <li
          key={index}
          dangerouslySetInnerHTML={{ __html: item.replace("$experience", exp) }}
        />
      ))}
    </ul>
  );
};

class Resume extends Component {
  constructor(props) {
    super(props);
    this.state = { showMorePersonalDetails: false };
  }

  toggleShowMoreDetails() {
    this.setState({
      showMorePersonalDetails: !this.state.showMorePersonalDetails,
    });
  }

  render() {
    return (
      <Page
        content={
          <Wrapper>
            <BigHeading>{profile.name}</BigHeading>
            <HorizontalAligned id="personal-info">
              <Linkify
                url={profile.socialPresence.email}
                text=<IconText>
                  <GrMail /> &nbsp; {profile.socialPresence.email.split(":")[1]}
                </IconText>
                color={(props) => props.theme.backgroundColor}
              />
              <Linkify
                url={profile.socialPresence.phone}
                text=<IconText>
                  <FaMobileAlt />
                  &nbsp;
                  {profile.socialPresence.phone.split(":")[1]}
                </IconText>
                color={(props) => props.theme.backgroundColor}
              />
              <Linkify
                url={profile.socialPresence.map}
                text=<IconText>{profile.address}</IconText>
                color={(props) => props.theme.backgroundColor}
              />
            </HorizontalAligned>
            <Button onClick={() => this.toggleShowMoreDetails()}>
              more personal details...
            </Button>
            <PersonalDetails show={this.state.showMorePersonalDetails}>
              <Text>
                Age: {age(moment(profile.dateOfBirth, "YYYY-MM-DD"))[0]}
              </Text>
              <Text>Gender: {profile.gender}</Text>
              <Text>Maritial Status: {profile.maritalStatus}</Text>
              <Text>Nationality : {profile.nationality}</Text>
            </PersonalDetails>
            <br />
            <br />
            <section id="education">
              <SubHeading>Education</SubHeading>
              <UnorderedList content={profile.education} />
            </section>

            <section id="summary">
              <SubHeading>Professional Summary</SubHeading>
              <UnorderedList content={profile.professionalSummary} />
            </section>

            <section id="skills">
              <SubHeading>Relevant skills</SubHeading>
              <UnorderedList content={profile.relevantSkills} />
            </section>
            <Heading>Professional Experience</Heading>
            <section id="work-experience">
              {experience.map((org, index) => (
                <div key={index}>
                  <hr />
                  <HorizontalAligned>
                    <SubHeading>{org.position}</SubHeading>
                    <SubHeading>
                      <Linkify
                        url={org.organization.url}
                        text={org.organization.name}
                      />
                    </SubHeading>
                    <SmallHeading>
                      {"(" +
                        moment(org.dateOfStarting, "YYYY-MM-DD").format(
                          "MMM YYYY"
                        ) +
                        " - " +
                        (org.dateOfEnding == null
                          ? "Present"
                          : moment(org.dateOfEnding, "YYYY-MM-DD").format(
                              "MMM YYYY"
                            )) +
                        ")"}
                    </SmallHeading>
                  </HorizontalAligned>
                  <hr />
                  {Object.entries(org.jobDescription).map((jd, index) => (
                    <div key={index}>
                      <SubHeading>{jd[0]}</SubHeading>
                      <UnorderedList content={jd[1]} />
                    </div>
                  ))}
                  <SubHeading>Tech Stack</SubHeading>
                  <UnorderedList content={org.techStack} />
                </div>
              ))}
            </section>
          </Wrapper>
        }
      />
    );
  }
}
export default Resume;
