import moment from "moment";
import React from "react";

export function age(from) {
  var years = moment().diff(from, "years");
  var month = moment().diff(from, "months");
  var days = moment().diff(from, "days");
  return [years + " year(s) ", month + " months ", days + " days "];
}
export const Linkify = (props) => {
  return (
    <a
      target="_blank"
      rel="noopener noreferrer"
      href={props.url}
      style={{ color: props.color, textDecoration: "none" }}
    >
      {props.text}
    </a>
  );
};
