import React, { Component } from "react";
import Page from "./page";
import { profile } from "../data/siteConfig";
import axios from "axios";
import ReactMarkdown from "react-markdown";
import styled from "styled-components";
import Loader from "../components/loader";

const Heading = styled.h2`
  margin: 0;
`;

class Blog extends Component {
  state = {
    posts: [],
  };

  async fetchBlogs() {
    axios
      .get(`https://api.github.com/users/${profile.userName}/gists`)
      .then((blogs) => {
        blogs.data.map(async (item) => {
          let name = Object.keys(item.files)[0];
          let content = String(
            (await axios.get(item.files[name].raw_url)).data
          );
          if (name.endsWith(".md")) {
            this.setState({
              posts: this.state.posts.concat({
                id: item.id,
                name: name,
                created: item.created_at,
                content: content,
              }),
            });
          }
        });
      });
  }

  componentDidMount() {
    // this.fetchBlogs();
  }

  render() {
    return (
      <Page
        content={
          this.state.posts.length === 0 ? (
            <Loader />
          ) : (
            this.state.posts.map((post, index) => (
              <React.Fragment key={post.id}>
                <Heading>{post.name}</Heading>
                <div key={index}>
                  {<ReactMarkdown source={post.content} escapeHtml={false} />}
                </div>
                <hr />
              </React.Fragment>
            ))
          )
        }
      />
    );
  }
}

export default Blog;
